


































module.exports =
  name: 'appMenu'
  data: ->
    mobileMenu: false

  mounted: ->
    @$watch 'route', =>
      @mobileMenu = false

  computed:
    route: -> return @$store.state.route.fullPath
  methods:
    assetPath: (image)-> return require('@/assets/' + image)




