



















module.exports =
  name: 'toast'

  data: ->
    duration: 7000
    showToast: false

  mounted: ->
    @$watch 'toast', =>
      @flashToast()

  methods:
    flashToast: ->
      if @showToast == true then return
      else
        @showToast = true
        clearTimeout(timeout)
        timeout = setTimeout ( =>
          @showToast = false
        ), @duration

  computed:
    error: ->
      if @toast.code is 200 then return false
      else return true
    hasLogs: ->
      if @toast.errors and Object.keys(@toast.errors).length != 0
        return true
      else return false
    toast: -> return @$store.state.toastMessage




