
































module.exports =
  name: 'landing'

  #transition storage
  data: ->
    transitionLinks: false
    companyHeader: false

  computed:
    projects: -> return @$store.state.projects

  methods:
    imagePath: (image)-> return "https://storage.googleapis.com/kats_images/#{image}"




